@tailwind base;
@tailwind components;
@tailwind utilities;

/* Optional: Additional custom global styles */
body {
  @apply bg-[#dbe4f0]; /* Matches your app background */
  font-family: 'Inter', sans-serif;
}

/* Premium light theme tokens */
:root {
  /* Backgrounds */
  --pl-bg: #f7fafc;         /* soft off-white */
  --pl-surface: #ffffff;    /* card surface */
  --pl-panel: #f3f6fb;      /* subtle panel */

  /* Primary accents */
  --pl-accent: #0f62fe;     /* vivid blue */
  --pl-accent-2: #4f8cff;   /* lighter accent */
  --pl-accent-3: #0b5ed7;   /* deeper accent */

  /* Text */
  --pl-text: #0b1220;       /* near-black for headings */
  --pl-body: #334155;       /* slate for body text */
  --pl-muted: #6b7280;      /* muted */

  /* Borders & shadows */
  --pl-border: rgba(15,23,42,0.06);
  --pl-border-accent: rgba(15,98,254,0.08);
  --pl-shadow: 0 6px 30px rgba(15,23,42,0.06);
  --pl-shadow-soft: 0 4px 18px rgba(15,23,42,0.04);

  /* Hover / interactive */
  --pl-hover: rgba(15,98,254,0.04);

  /* Gradients */
  --pl-gradient: linear-gradient(135deg, var(--pl-accent), var(--pl-accent-2));
  --pl-surface-gradient: linear-gradient(180deg, var(--pl-surface), var(--pl-panel));
}

/* Subtle texture for premium depth */
.pl-texture {
  background-image: linear-gradient(rgba(11,98,254,0.01) 1px, transparent 1px), linear-gradient(90deg, rgba(11,98,254,0.01) 1px, transparent 1px);
  background-size: 28px 28px;
}

/* UI helper components (centralized dark-mode styles for common controls) */
@layer components {
  .ui-btn {
    @apply px-4 py-3 rounded-xl text-center transition-all duration-300 flex items-center justify-center gap-2 font-medium;
  }

  /* Primary filled button (light uses gradient, dark uses translucent surface) */
  .ui-btn--primary {
    background: var(--pl-gradient);
    color: white;
    border: 1px solid var(--pl-border-accent);
    box-shadow: var(--pl-shadow);
  }

  .dark .ui-btn--primary {
    background: rgba(255,255,255,0.03);
    color: white;
    border: 1px solid rgba(255,255,255,0.04);
    box-shadow: 0 6px 20px rgba(2,6,23,0.6);
  }

  /* Surface / outline style for secondary actions (light: surface, dark: translucent) */
  .ui-btn--surface {
    background: var(--pl-surface);
    color: var(--pl-body);
    border: 1px solid var(--pl-border);
    box-shadow: var(--pl-shadow-soft);
  }

  .dark .ui-btn--surface {
    background: rgba(255,255,255,0.02);
    color: rgba(255,255,255,0.95);
    border: 1px solid rgba(255,255,255,0.03);
    box-shadow: 0 6px 18px rgba(2,6,23,0.5);
  }

  /* Accent surface where the text uses the brand accent color */
  .ui-btn--accent {
    background: var(--pl-surface);
    color: var(--pl-accent);
    border: 1px solid var(--pl-border);
    box-shadow: var(--pl-shadow-soft);
  }

  .dark .ui-btn--accent {
    background: rgba(255,255,255,0.02);
    color: var(--pl-accent);
    border: 1px solid rgba(255,255,255,0.03);
    box-shadow: 0 6px 18px rgba(2,6,23,0.5);
  }
}
